import React, { useState } from "react";
import {getStatus, updateStatus} from  '../../../../../store/profile-reduser'
import Profile_about_top from "./Profile_about_top";

import * as axios from "axios";
import { connect } from "react-redux";
const config = {
  headers: {
    "Access-Control-Allow-Origin": "*",
    "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
  },
};



class Profile_about_top_cont extends React.Component {
  componentDidMount(){
    this.props.getStatus()
  }
   render(){
    return <Profile_about_top stat = {this.props.status} updateStatus ={this.props.updateStatus}/>;
}};

let mapStateToProps = (state) =>{
 return{
    status: state.Profile.status
}
}
let mapDispatchToProps = {
  getStatus,
  updateStatus
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile_about_top_cont);
