import * as axios from "axios";
const instance = axios.create({
  baseURL: "https://dry-headland-66248.herokuapp.com/api/",
});

export const ProfileAPI = {
    getAPIStatus(){
        return instance.get("https://dry-headland-66248.herokuapp.com/api/Ttatus")
    },
    updateAPIStatus(status) {
      return instance.put(`https://dry-headland-66248.herokuapp.com/api/Ttatus/5f2874b6b2bca231063571dc`, { status: status });
    },
}