import { ProfileAPI } from "../API/api";

const SET_STATUS = 'SET_STATUS'


let initialState = {
  status: ''
}

export const profileReducer = (state = initialState, action) => {
    switch (action.type) {
      case SET_STATUS:{
       return{
           ...state,
           status: action.status
       }

      }
      default:
        return state;
    }
  };



  export const setStatus = (status) => {
    return {
      type: SET_STATUS,
      status,
    };
  };

  export const getStatus = () => {
    return  (dispatch) => {
     ProfileAPI.getAPIStatus().then((response) => {
        dispatch(setStatus(response.data[0].status));
      });
    };
  };
  export const updateStatus = (status) => {
    return (dispatch) => {
      ProfileAPI.updateAPIStatus(status).then((response) => {
          dispatch(setStatus(status));
      });
    };
  };